import React, { Component } from "react";
import "./UserCard.scss";
import Select from "./../UI/Select";
// import "bootstrap/dist/css/bootstrap.min.css";
import { connect } from "react-redux";

const UserCard = props => {
  const { img, name, id } = props.userDetails;
  const optionsList = Object.keys(props.usersList).map((key, index) => {
    return { label: props.usersList[key].name, value: key };
  });
  const userCgangeHandler = e => {
    const val = e.target.value;
    props.onUserChange(val);
  };
  return (
    <div className="user-card-wrapp">
      <div className="avatar">
        <img src={img} alt="avatar" />
      </div>
      <div className="user-name">
        <Select
          optopns={optionsList}
          value={id}
          onHandleChange={userCgangeHandler}
        />
      </div>
    </div>
  );
};

const mapStateToProps = state => {
  const { activeUserId } = state;
  return {
    userDetails: state.usersList[activeUserId],
    usersList: state.usersList
    // name: state.usersList[userid].name
  };
};
// const mapDispatchToProps = { onUserIdChange };
const mapDispatchToProps = dispatch => ({
  onUserChange: id =>
    dispatch({
      type: "ON_USER_CHANGE",
      payload: id
    })
});
export default connect(mapStateToProps, mapDispatchToProps)(UserCard);
